"use client";

import React, { useState, useEffect } from "react";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

interface Message {
  role: "user" | "assistant";
  content: string;
  timestamp: Date;
}

interface DiagnosisModalProps {
  conversation: Message[];
  onClose: () => void;
}

interface DiagnosisResult {
  overallAssessment: string;
  indicators: {
    category: string;
    level: "low" | "moderate" | "high";
    description: string;
  }[];
  recommendations: string[];
  riskLevel: "low" | "moderate" | "high";
}

export default function DiagnosisModal({ conversation, onClose }: DiagnosisModalProps) {
  const [diagnosis, setDiagnosis] = useState<DiagnosisResult | null>(null);
  const [isAnalyzing, setIsAnalyzing] = useState(true);
  const [therapistEmail, setTherapistEmail] = useState("");
  const [shareMessage, setShareMessage] = useState("");
  const [shareStatus, setShareStatus] = useState("");
  const [isSharing, setIsSharing] = useState(false);

  useEffect(() => {
    analyzeMentalHealth();
  }, [conversation]);

  const analyzeMentalHealth = () => {
    setIsAnalyzing(true);
    
    // Simulate analysis delay
    setTimeout(() => {
      const userMessages = conversation.filter(msg => msg.role === "user");
      const fullText = userMessages.map(msg => msg.content).join(" ").toLowerCase();
      
      // Simple keyword-based analysis (in production, this would use AI)
      const indicators: {
        category: string;
        level: "low" | "moderate" | "high";
        description: string;
      }[] = [];
      let riskLevel: "low" | "moderate" | "high" = "low";
      
      // Analyze for depression indicators
      const depressionKeywords = ["sad", "depressed", "hopeless", "worthless", "empty", "lonely", "tired", "exhausted"];
      const depressionCount = depressionKeywords.filter(word => fullText.includes(word)).length;
      
      if (depressionCount > 0) {
        indicators.push({
          category: "Mood Indicators",
          level: (depressionCount > 2 ? "high" : "moderate") as "high" | "moderate",
          description: `Detected ${depressionCount} mood-related concerns in conversation`
        });
        if (depressionCount > 2) riskLevel = "high";
        else if (riskLevel === "low") riskLevel = "moderate";
      }
      
      // Analyze for anxiety indicators
      const anxietyKeywords = ["anxious", "worried", "nervous", "panic", "scared", "overwhelmed", "stress"];
      const anxietyCount = anxietyKeywords.filter(word => fullText.includes(word)).length;
      
      if (anxietyCount > 0) {
        indicators.push({
          category: "Anxiety Indicators",
          level: (anxietyCount > 2 ? "high" : "moderate") as "high" | "moderate",
          description: `Identified ${anxietyCount} anxiety-related expressions`
        });
        if (anxietyCount > 2 && riskLevel !== "high") riskLevel = "high";
        else if (riskLevel === "low") riskLevel = "moderate";
      }
      
      // Analyze for sleep/energy issues
      const sleepKeywords = ["can't sleep", "insomnia", "tired", "exhausted", "no energy"];
      const sleepCount = sleepKeywords.filter(word => fullText.includes(word)).length;
      
      if (sleepCount > 0) {
        indicators.push({
          category: "Sleep & Energy",
          level: (sleepCount > 1 ? "moderate" : "low") as "moderate" | "low",
          description: `Sleep or energy concerns mentioned ${sleepCount} times`
        });
      }
      
      // Check conversation patterns
      const avgMessageLength = userMessages.reduce((sum, msg) => sum + msg.content.length, 0) / userMessages.length;
      if (avgMessageLength < 20) {
        indicators.push({
          category: "Communication Pattern",
          level: "moderate",
          description: "Brief responses may indicate low energy or withdrawal"
        });
      }
      
      // Generate recommendations
      const recommendations = [];
      if (riskLevel === "high") {
        recommendations.push("Consider reaching out to a mental health professional");
        recommendations.push("Contact a crisis helpline if you're having thoughts of self-harm");
      }
      if (riskLevel === "moderate" || riskLevel === "high") {
        recommendations.push("Practice self-care activities like exercise, meditation, or hobbies");
        recommendations.push("Maintain regular sleep schedule and healthy eating habits");
        recommendations.push("Stay connected with supportive friends and family");
      }
      recommendations.push("Continue regular check-ins with this AI assistant for support");
      
      let overallAssessment = "";
      if (riskLevel === "low") {
        overallAssessment = "The conversation suggests generally stable mental health with minor concerns that are within normal range.";
      } else if (riskLevel === "moderate") {
        overallAssessment = "Some indicators suggest you may be experiencing mild to moderate mental health challenges that could benefit from attention.";
      } else {
        overallAssessment = "Multiple indicators suggest significant mental health concerns that warrant professional attention and support.";
      }
      
      setDiagnosis({
        overallAssessment,
        indicators,
        recommendations,
        riskLevel
      });
      
      setIsAnalyzing(false);
    }, 2000);
  };

  const handleShare = async () => {
    if (!therapistEmail.match(/^\S+@\S+\.\S+$/)) {
      setShareStatus("Please enter a valid email address.");
      return;
    }
    
    setIsSharing(true);
    
    // Simulate secure sharing (in production, this would call a secure API)
    setTimeout(() => {
      setShareStatus("Session summary and insights have been securely sent to your therapist.");
      setIsSharing(false);
    }, 1500);
  };

  const getRiskLevelColor = (level: string) => {
    switch (level) {
      case "low": return "bg-green-100 text-green-800 border-green-200";
      case "moderate": return "bg-yellow-100 text-yellow-800 border-yellow-200";
      case "high": return "bg-red-100 text-red-800 border-red-200";
      default: return "bg-gray-100 text-gray-800 border-gray-200";
    }
  };

  return (
    <Dialog open onOpenChange={onClose}>
      <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>Mental Health Insights</DialogTitle>
          <DialogDescription>
            AI-powered analysis based on your conversation patterns and expressions
          </DialogDescription>
        </DialogHeader>

        {isAnalyzing ? (
          <div className="flex items-center justify-center py-8">
            <div className="text-center space-y-4">
              <div className="flex justify-center space-x-1">
                <div className="w-3 h-3 bg-primary rounded-full animate-bounce"></div>
                <div className="w-3 h-3 bg-primary rounded-full animate-bounce" style={{ animationDelay: "0.1s" }}></div>
                <div className="w-3 h-3 bg-primary rounded-full animate-bounce" style={{ animationDelay: "0.2s" }}></div>
              </div>
              <p className="text-sm text-muted-foreground">Analyzing conversation patterns...</p>
            </div>
          </div>
        ) : diagnosis && (
          <div className="space-y-6">
            {/* Overall Assessment */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center justify-between">
                  Overall Assessment
                  <Badge className={getRiskLevelColor(diagnosis.riskLevel)}>
                    {diagnosis.riskLevel.toUpperCase()} RISK
                  </Badge>
                </CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-sm leading-relaxed">{diagnosis.overallAssessment}</p>
              </CardContent>
            </Card>

            {/* Indicators */}
            {diagnosis.indicators.length > 0 && (
              <Card>
                <CardHeader>
                  <CardTitle>Detected Indicators</CardTitle>
                </CardHeader>
                <CardContent className="space-y-3">
                  {diagnosis.indicators.map((indicator, index) => (
                    <div key={index} className="flex items-start justify-between p-3 bg-secondary/20 rounded-lg">
                      <div className="flex-1">
                        <h4 className="font-medium text-sm">{indicator.category}</h4>
                        <p className="text-xs text-muted-foreground mt-1">{indicator.description}</p>
                      </div>
                      <Badge variant="outline" className={getRiskLevelColor(indicator.level)}>
                        {indicator.level}
                      </Badge>
                    </div>
                  ))}
                </CardContent>
              </Card>
            )}

            {/* Recommendations */}
            <Card>
              <CardHeader>
                <CardTitle>Recommendations</CardTitle>
              </CardHeader>
              <CardContent>
                <ul className="space-y-2">
                  {diagnosis.recommendations.map((rec, index) => (
                    <li key={index} className="text-sm flex items-start">
                      <span className="w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></span>
                      {rec}
                    </li>
                  ))}
                </ul>
              </CardContent>
            </Card>

            {/* Therapist Sharing */}
            <Card>
              <CardHeader>
                <CardTitle>Share with Therapist</CardTitle>
                <DialogDescription>
                  Securely share this session summary and insights with your mental health professional
                </DialogDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label htmlFor="therapist-email">Therapist Email</Label>
                  <Input
                    id="therapist-email"
                    type="email"
                    value={therapistEmail}
                    onChange={(e) => setTherapistEmail(e.target.value)}
                    placeholder="therapist@example.com"
                  />
                </div>
                <div>
                  <Label htmlFor="share-message">Additional Message (Optional)</Label>
                  <Textarea
                    id="share-message"
                    value={shareMessage}
                    onChange={(e) => setShareMessage(e.target.value)}
                    placeholder="Any additional context you'd like to share..."
                    rows={3}
                  />
                </div>
                {shareStatus && (
                  <p className={`text-sm ${shareStatus.includes("sent") ? "text-green-600" : "text-red-600"}`}>
                    {shareStatus}
                  </p>
                )}
                <Button 
                  onClick={handleShare} 
                  disabled={isSharing || !therapistEmail}
                  className="w-full"
                >
                  {isSharing ? "Sending..." : "Send Secure Summary"}
                </Button>
              </CardContent>
            </Card>
          </div>
        )}

        <div className="flex justify-end pt-4">
          <Button variant="outline" onClick={onClose}>
            Close
          </Button>
        </div>

        <div className="text-xs text-muted-foreground text-center pt-2 border-t">
          <p>This analysis is for informational purposes only and does not replace professional medical advice.</p>
        </div>
      </DialogContent>
    </Dialog>
  );
}
