"use client";

import React, { useEffect, useRef, useState } from "react";
import { Card } from "@/components/ui/card";

// Type declarations for Speech Recognition API
interface SpeechRecognitionEvent extends Event {
  results: SpeechRecognitionResultList;
  resultIndex: number;
}

interface SpeechRecognitionErrorEvent extends Event {
  error: string;
}

interface VoiceInputProps {
  onResult: (transcript: string) => void;
  onError: (error: string) => void;
  language?: string;
}

export default function VoiceInput({ onResult, onError, language = "en-US" }: VoiceInputProps) {
  const [isListening, setIsListening] = useState(false);
  const [transcript, setTranscript] = useState("");
  const recognitionRef = useRef<any>(null);

  useEffect(() => {
    // Check for browser support
    const SpeechRecognition = 
      (window as any).SpeechRecognition || 
      (window as any).webkitSpeechRecognition;

    if (!SpeechRecognition) {
      onError("Speech Recognition is not supported in this browser. Please try Chrome, Edge, or Safari.");
      return;
    }

    const recognition = new SpeechRecognition();
    recognition.continuous = true;
    recognition.interimResults = true;
    recognition.lang = language;

    recognition.onstart = () => {
      setIsListening(true);
      setTranscript("");
    };

    recognition.onresult = (event: SpeechRecognitionEvent) => {
      let interimTranscript = "";
      let finalTranscript = "";

      for (let i = event.resultIndex; i < event.results.length; i++) {
        const transcript = event.results[i][0].transcript;
        if (event.results[i].isFinal) {
          finalTranscript += transcript;
        } else {
          interimTranscript += transcript;
        }
      }

      setTranscript(finalTranscript + interimTranscript);

      // If we have a final result, send it
      if (finalTranscript) {
        onResult(finalTranscript.trim());
        setTranscript("");
      }
    };

    recognition.onerror = (event: SpeechRecognitionErrorEvent) => {
      console.error("Speech recognition error:", event.error);
      setIsListening(false);
      
      let errorMessage = "Speech recognition error occurred.";
      switch (event.error) {
        case "no-speech":
          errorMessage = "No speech detected. Please try speaking again.";
          break;
        case "audio-capture":
          errorMessage = "Microphone access denied. Please allow microphone access and try again.";
          break;
        case "not-allowed":
          errorMessage = "Microphone permission denied. Please enable microphone access in your browser settings.";
          break;
        case "network":
          errorMessage = "Network error occurred. Please check your internet connection.";
          break;
        default:
          errorMessage = `Speech recognition error: ${event.error}`;
      }
      
      onError(errorMessage);
    };

    recognition.onend = () => {
      setIsListening(false);
    };

    recognitionRef.current = recognition;

    // Start recognition
    try {
      recognition.start();
    } catch (error) {
      console.error("Failed to start speech recognition:", error);
      onError("Failed to start speech recognition. Please try again.");
    }

    // Cleanup function
    return () => {
      if (recognitionRef.current) {
        recognitionRef.current.stop();
      }
    };
  }, [language, onResult, onError]);

  // Stop recognition when component unmounts or when parent stops listening
  useEffect(() => {
    return () => {
      if (recognitionRef.current) {
        recognitionRef.current.stop();
      }
    };
  }, []);

  return (
    <div className="fixed bottom-24 left-4 right-4 z-50">
      <Card className="p-4 bg-card/95 backdrop-blur-sm border-2 border-primary/20">
        <div className="flex items-center justify-center space-x-4">
          <div className="flex items-center space-x-2">
            <div className="relative">
              <div className="w-4 h-4 bg-red-500 rounded-full animate-pulse"></div>
              {isListening && (
                <div className="absolute inset-0 w-4 h-4 bg-red-500 rounded-full animate-ping"></div>
              )}
            </div>
            <span className="text-sm font-medium">
              {isListening ? "Listening..." : "Initializing..."}
            </span>
          </div>
          
          {transcript && (
            <div className="flex-1 text-center">
              <p className="text-sm text-muted-foreground italic">
                "{transcript}"
              </p>
            </div>
          )}
        </div>
        
        <div className="mt-2 text-xs text-center text-muted-foreground">
          Speak naturally - I'll process your words when you pause
        </div>
      </Card>
    </div>
  );
}
