"use client";

import React, { useState, useEffect, useRef } from "react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import VoiceInput from "./VoiceInput";
import DiagnosisModal from "./DiagnosisModal";
import VoiceSettings from "./VoiceSettings";
import SessionSummary from "./SessionSummary";

interface Message {
  role: "user" | "assistant";
  content: string;
  timestamp: Date;
}

export default function AssistantCall() {
  const [conversation, setConversation] = useState<Message[]>([]);
  const [isListening, setIsListening] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [showDiagnosis, setShowDiagnosis] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [showSummary, setShowSummary] = useState(false);
  const [voiceSettings, setVoiceSettings] = useState({
    voice: null as SpeechSynthesisVoice | null,
    rate: 1,
    pitch: 1,
    language: "en-US"
  });
  
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const conversationRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [conversation]);

  // Initialize with welcome message
  useEffect(() => {
    const welcomeMessage: Message = {
      role: "assistant",
      content: "Hello! I'm here to listen and support you. Feel free to share what's on your mind, or we can just have a friendly conversation. How are you feeling today?",
      timestamp: new Date()
    };
    setConversation([welcomeMessage]);
    
    // Speak welcome message
    setTimeout(() => {
      speakMessage(welcomeMessage.content);
    }, 1000);
  }, []);

  const speakMessage = (text: string) => {
    if ('speechSynthesis' in window) {
      // Cancel any ongoing speech
      speechSynthesis.cancel();
      
      const utterance = new SpeechSynthesisUtterance(text);
      
      if (voiceSettings.voice) {
        utterance.voice = voiceSettings.voice;
      }
      utterance.rate = voiceSettings.rate;
      utterance.pitch = voiceSettings.pitch;
      utterance.lang = voiceSettings.language;
      
      speechSynthesis.speak(utterance);
    }
  };

  const sendMessage = async (userMessage: string) => {
    const userMsg: Message = {
      role: "user",
      content: userMessage,
      timestamp: new Date()
    };
    
    const newConversation = [...conversation, userMsg];
    setConversation(newConversation);
    setIsLoading(true);

    try {
      const response = await fetch("/api/assistant", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          messages: newConversation.map(msg => ({
            role: msg.role,
            content: msg.content
          }))
        }),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      const assistantReply = data?.choices?.[0]?.message?.content || "I'm sorry, I didn't catch that. Could you please try again?";
      
      const assistantMsg: Message = {
        role: "assistant",
        content: assistantReply,
        timestamp: new Date()
      };
      
      const updatedConversation = [...newConversation, assistantMsg];
      setConversation(updatedConversation);
      
      // Speak the assistant's response
      setTimeout(() => {
        speakMessage(assistantReply);
      }, 500);
      
    } catch (error) {
      console.error("Error sending message:", error);
      const errorMsg: Message = {
        role: "assistant",
        content: "I'm having trouble connecting right now. Please check your internet connection and try again.",
        timestamp: new Date()
      };
      setConversation(prev => [...prev, errorMsg]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleVoiceResult = (transcript: string) => {
    if (transcript.trim()) {
      sendMessage(transcript);
    }
  };

  const handleVoiceError = (error: string) => {
    console.error("Voice input error:", error);
    setIsListening(false);
  };

  const toggleListening = () => {
    if (isListening) {
      setIsListening(false);
    } else {
      setIsListening(true);
    }
  };

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  return (
    <div className="flex flex-col h-full max-w-4xl mx-auto">
      {/* Conversation Area */}
      <div 
        ref={conversationRef}
        className="flex-1 p-4 overflow-y-auto space-y-4 bg-gradient-to-b from-background to-secondary/10"
      >
        {conversation.map((message, index) => (
          <div
            key={index}
            className={`flex ${message.role === "user" ? "justify-end" : "justify-start"}`}
          >
            <Card className={`max-w-md p-4 ${
              message.role === "user" 
                ? "bg-primary text-primary-foreground ml-12" 
                : "bg-card text-card-foreground mr-12"
            }`}>
              <div className="space-y-2">
                <p className="text-sm leading-relaxed">{message.content}</p>
                <p className={`text-xs opacity-70 ${
                  message.role === "user" ? "text-right" : "text-left"
                }`}>
                  {formatTime(message.timestamp)}
                </p>
              </div>
            </Card>
          </div>
        ))}
        
        {isLoading && (
          <div className="flex justify-start">
            <Card className="bg-card text-card-foreground mr-12 p-4">
              <div className="flex items-center space-x-2">
                <div className="flex space-x-1">
                  <div className="w-2 h-2 bg-primary rounded-full animate-bounce"></div>
                  <div className="w-2 h-2 bg-primary rounded-full animate-bounce" style={{ animationDelay: "0.1s" }}></div>
                  <div className="w-2 h-2 bg-primary rounded-full animate-bounce" style={{ animationDelay: "0.2s" }}></div>
                </div>
                <span className="text-sm text-muted-foreground">Thinking...</span>
              </div>
            </Card>
          </div>
        )}
        
        <div ref={messagesEndRef} />
      </div>

      {/* Control Panel */}
      <div className="p-4 border-t border-border bg-card/50 backdrop-blur-sm">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-3">
            <Button
              onClick={toggleListening}
              variant={isListening ? "destructive" : "default"}
              size="lg"
              className="rounded-full px-6"
              disabled={isLoading}
            >
              {isListening ? "Stop Listening" : "Start Conversation"}
            </Button>
            
            {isListening && (
              <div className="flex items-center space-x-2 text-sm text-muted-foreground">
                <div className="w-2 h-2 bg-red-500 rounded-full animate-pulse"></div>
                <span>Listening...</span>
              </div>
            )}
          </div>

          <div className="flex items-center space-x-2">
            <Button
              variant="outline"
              size="sm"
              onClick={() => setShowSummary(true)}
              disabled={conversation.length <= 1}
            >
              Summary
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => setShowDiagnosis(true)}
              disabled={conversation.length <= 1}
            >
              Insights
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => setShowSettings(true)}
            >
              Voice Settings
            </Button>
          </div>
        </div>
      </div>

      {/* Voice Input Component */}
      {isListening && (
        <VoiceInput
          onResult={handleVoiceResult}
          onError={handleVoiceError}
          language={voiceSettings.language}
        />
      )}

      {/* Modals */}
      {showDiagnosis && (
        <DiagnosisModal
          conversation={conversation}
          onClose={() => setShowDiagnosis(false)}
        />
      )}

      {showSettings && (
        <VoiceSettings
          settings={voiceSettings}
          onSettingsChange={setVoiceSettings}
          onClose={() => setShowSettings(false)}
        />
      )}

      {showSummary && (
        <SessionSummary
          conversation={conversation}
          onClose={() => setShowSummary(false)}
        />
      )}
    </div>
  );
}
