"use client";

import React, { useState, useEffect } from "react";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";

interface Message {
  role: "user" | "assistant";
  content: string;
  timestamp: Date;
}

interface SessionSummaryProps {
  conversation: Message[];
  onClose: () => void;
}

interface SessionStats {
  duration: string;
  messageCount: number;
  userMessages: number;
  assistantMessages: number;
  averageResponseTime: string;
  topicsDiscussed: string[];
  emotionalTone: "positive" | "neutral" | "concerning";
  keyInsights: string[];
}

export default function SessionSummary({ conversation, onClose }: SessionSummaryProps) {
  const [summary, setSummary] = useState<SessionStats | null>(null);
  const [isGenerating, setIsGenerating] = useState(true);

  useEffect(() => {
    generateSummary();
  }, [conversation]);

  const generateSummary = () => {
    setIsGenerating(true);
    
    // Simulate processing time
    setTimeout(() => {
      if (conversation.length === 0) {
        setIsGenerating(false);
        return;
      }

      const userMessages = conversation.filter(msg => msg.role === "user");
      const assistantMessages = conversation.filter(msg => msg.role === "assistant");
      
      // Calculate session duration
      const startTime = conversation[0]?.timestamp;
      const endTime = conversation[conversation.length - 1]?.timestamp;
      const durationMs = endTime && startTime ? endTime.getTime() - startTime.getTime() : 0;
      const durationMinutes = Math.floor(durationMs / 60000);
      const durationSeconds = Math.floor((durationMs % 60000) / 1000);
      const duration = `${durationMinutes}m ${durationSeconds}s`;

      // Analyze conversation content
      const allText = conversation.map(msg => msg.content).join(" ").toLowerCase();
      
      // Detect topics
      const topicKeywords = {
        "Mental Health": ["anxiety", "depression", "stress", "worried", "sad", "mental", "therapy"],
        "Relationships": ["family", "friend", "partner", "relationship", "love", "conflict"],
        "Work/Career": ["work", "job", "career", "boss", "colleague", "office", "professional"],
        "Health": ["health", "sleep", "tired", "energy", "physical", "body"],
        "Personal Growth": ["goal", "future", "change", "improve", "growth", "development"],
        "Daily Life": ["day", "routine", "daily", "morning", "evening", "schedule"]
      };

      const detectedTopics: string[] = [];
      Object.entries(topicKeywords).forEach(([topic, keywords]) => {
        if (keywords.some(keyword => allText.includes(keyword))) {
          detectedTopics.push(topic);
        }
      });

      // Determine emotional tone
      const positiveWords = ["good", "great", "happy", "better", "positive", "excited", "grateful"];
      const concerningWords = ["terrible", "awful", "hopeless", "suicidal", "harm", "crisis"];
      
      const positiveCount = positiveWords.filter(word => allText.includes(word)).length;
      const concerningCount = concerningWords.filter(word => allText.includes(word)).length;
      
      let emotionalTone: "positive" | "neutral" | "concerning" = "neutral";
      if (concerningCount > 0) {
        emotionalTone = "concerning";
      } else if (positiveCount > concerningCount) {
        emotionalTone = "positive";
      }

      // Generate key insights
      const insights: string[] = [];
      
      if (userMessages.length > 0) {
        const avgMessageLength = userMessages.reduce((sum, msg) => sum + msg.content.length, 0) / userMessages.length;
        if (avgMessageLength < 30) {
          insights.push("User provided brief responses, possibly indicating low energy or difficulty expressing thoughts");
        } else if (avgMessageLength > 100) {
          insights.push("User shared detailed thoughts and feelings, showing good engagement");
        }
      }

      if (detectedTopics.includes("Mental Health")) {
        insights.push("Mental health topics were discussed - consider follow-up sessions");
      }

      if (emotionalTone === "positive") {
        insights.push("Overall positive emotional tone detected throughout the conversation");
      } else if (emotionalTone === "concerning") {
        insights.push("Some concerning language detected - professional support may be beneficial");
      }

      if (conversation.length > 10) {
        insights.push("Extended conversation shows good therapeutic engagement");
      }

      // Calculate average response time (simulated)
      const avgResponseTime = "2.3s";

      const sessionStats: SessionStats = {
        duration,
        messageCount: conversation.length,
        userMessages: userMessages.length,
        assistantMessages: assistantMessages.length,
        averageResponseTime: avgResponseTime,
        topicsDiscussed: detectedTopics.length > 0 ? detectedTopics : ["General Conversation"],
        emotionalTone,
        keyInsights: insights.length > 0 ? insights : ["Session completed successfully"]
      };

      setSummary(sessionStats);
      setIsGenerating(false);
    }, 1500);
  };

  const getEmotionalToneColor = (tone: string) => {
    switch (tone) {
      case "positive": return "bg-green-100 text-green-800 border-green-200";
      case "concerning": return "bg-red-100 text-red-800 border-red-200";
      default: return "bg-blue-100 text-blue-800 border-blue-200";
    }
  };

  const downloadSummary = () => {
    if (!summary) return;

    const summaryText = `
AI Assistant Session Summary
Generated: ${new Date().toLocaleString()}

SESSION STATISTICS:
- Duration: ${summary.duration}
- Total Messages: ${summary.messageCount}
- User Messages: ${summary.userMessages}
- Assistant Messages: ${summary.assistantMessages}
- Average Response Time: ${summary.averageResponseTime}

TOPICS DISCUSSED:
${summary.topicsDiscussed.map(topic => `- ${topic}`).join('\n')}

EMOTIONAL TONE: ${summary.emotionalTone.toUpperCase()}

KEY INSIGHTS:
${summary.keyInsights.map(insight => `- ${insight}`).join('\n')}

CONVERSATION TRANSCRIPT:
${conversation.map((msg, index) => 
  `[${msg.timestamp.toLocaleTimeString()}] ${msg.role.toUpperCase()}: ${msg.content}`
).join('\n\n')}

---
This summary is generated by AI and is for informational purposes only.
    `.trim();

    const blob = new Blob([summaryText], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `ai-assistant-session-${new Date().toISOString().split('T')[0]}.txt`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  return (
    <Dialog open onOpenChange={onClose}>
      <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>Session Summary</DialogTitle>
          <DialogDescription>
            Overview of your conversation with the AI assistant
          </DialogDescription>
        </DialogHeader>

        {isGenerating ? (
          <div className="flex items-center justify-center py-8">
            <div className="text-center space-y-4">
              <div className="flex justify-center space-x-1">
                <div className="w-3 h-3 bg-primary rounded-full animate-bounce"></div>
                <div className="w-3 h-3 bg-primary rounded-full animate-bounce" style={{ animationDelay: "0.1s" }}></div>
                <div className="w-3 h-3 bg-primary rounded-full animate-bounce" style={{ animationDelay: "0.2s" }}></div>
              </div>
              <p className="text-sm text-muted-foreground">Generating session summary...</p>
            </div>
          </div>
        ) : summary && (
          <div className="space-y-6">
            {/* Session Statistics */}
            <Card>
              <CardHeader>
                <CardTitle>Session Statistics</CardTitle>
              </CardHeader>
              <CardContent className="grid grid-cols-2 gap-4">
                <div className="space-y-1">
                  <p className="text-sm text-muted-foreground">Duration</p>
                  <p className="text-lg font-semibold">{summary.duration}</p>
                </div>
                <div className="space-y-1">
                  <p className="text-sm text-muted-foreground">Total Messages</p>
                  <p className="text-lg font-semibold">{summary.messageCount}</p>
                </div>
                <div className="space-y-1">
                  <p className="text-sm text-muted-foreground">Your Messages</p>
                  <p className="text-lg font-semibold">{summary.userMessages}</p>
                </div>
                <div className="space-y-1">
                  <p className="text-sm text-muted-foreground">AI Responses</p>
                  <p className="text-lg font-semibold">{summary.assistantMessages}</p>
                </div>
              </CardContent>
            </Card>

            {/* Topics and Tone */}
            <Card>
              <CardHeader>
                <CardTitle>Conversation Analysis</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <p className="text-sm text-muted-foreground mb-2">Topics Discussed</p>
                  <div className="flex flex-wrap gap-2">
                    {summary.topicsDiscussed.map((topic, index) => (
                      <Badge key={index} variant="secondary">
                        {topic}
                      </Badge>
                    ))}
                  </div>
                </div>
                
                <Separator />
                
                <div className="flex items-center justify-between">
                  <p className="text-sm text-muted-foreground">Emotional Tone</p>
                  <Badge className={getEmotionalToneColor(summary.emotionalTone)}>
                    {summary.emotionalTone.toUpperCase()}
                  </Badge>
                </div>
              </CardContent>
            </Card>

            {/* Key Insights */}
            <Card>
              <CardHeader>
                <CardTitle>Key Insights</CardTitle>
              </CardHeader>
              <CardContent>
                <ul className="space-y-2">
                  {summary.keyInsights.map((insight, index) => (
                    <li key={index} className="text-sm flex items-start">
                      <span className="w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></span>
                      {insight}
                    </li>
                  ))}
                </ul>
              </CardContent>
            </Card>

            {/* Actions */}
            <div className="flex justify-between pt-4">
              <Button variant="outline" onClick={downloadSummary}>
                Download Summary
              </Button>
              <Button onClick={onClose}>
                Close
              </Button>
            </div>
          </div>
        )}

        <div className="text-xs text-muted-foreground text-center pt-2 border-t">
          <p>This summary is generated by AI and is for informational purposes only.</p>
        </div>
      </DialogContent>
    </Dialog>
  );
}
