"use client";

import React, { useState, useEffect } from "react";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Slider } from "@/components/ui/slider";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

interface VoiceSettings {
  voice: SpeechSynthesisVoice | null;
  rate: number;
  pitch: number;
  language: string;
}

interface VoiceSettingsProps {
  settings: VoiceSettings;
  onSettingsChange: (settings: VoiceSettings) => void;
  onClose: () => void;
}

export default function VoiceSettings({ settings, onSettingsChange, onClose }: VoiceSettingsProps) {
  const [voices, setVoices] = useState<SpeechSynthesisVoice[]>([]);
  const [tempSettings, setTempSettings] = useState<VoiceSettings>(settings);
  const [isTestPlaying, setIsTestPlaying] = useState(false);

  useEffect(() => {
    const loadVoices = () => {
      const availableVoices = speechSynthesis.getVoices();
      setVoices(availableVoices);
      
      // If no voice is selected, try to find a good default
      if (!tempSettings.voice && availableVoices.length > 0) {
        // Try to find English voices first
        const englishVoices = availableVoices.filter(voice => 
          voice.lang.startsWith('en') && !voice.name.includes('Google')
        );
        const defaultVoice = englishVoices[0] || availableVoices[0];
        setTempSettings(prev => ({ ...prev, voice: defaultVoice }));
      }
    };

    loadVoices();
    
    // Some browsers load voices asynchronously
    if (speechSynthesis.onvoiceschanged !== undefined) {
      speechSynthesis.onvoiceschanged = loadVoices;
    }
  }, [tempSettings.voice]);

  const handleVoiceChange = (voiceName: string) => {
    const selectedVoice = voices.find(voice => voice.name === voiceName) || null;
    setTempSettings(prev => ({ ...prev, voice: selectedVoice }));
  };

  const handleLanguageChange = (language: string) => {
    setTempSettings(prev => ({ ...prev, language }));
    
    // Try to find a voice that matches the selected language
    const matchingVoice = voices.find(voice => voice.lang.startsWith(language.split('-')[0]));
    if (matchingVoice) {
      setTempSettings(prev => ({ ...prev, voice: matchingVoice }));
    }
  };

  const handleRateChange = (rate: number[]) => {
    setTempSettings(prev => ({ ...prev, rate: rate[0] }));
  };

  const handlePitchChange = (pitch: number[]) => {
    setTempSettings(prev => ({ ...prev, pitch: pitch[0] }));
  };

  const testVoice = () => {
    if (isTestPlaying) {
      speechSynthesis.cancel();
      setIsTestPlaying(false);
      return;
    }

    setIsTestPlaying(true);
    
    const testText = tempSettings.language.startsWith('id') 
      ? "Halo, saya adalah asisten AI Anda. Bagaimana perasaan Anda hari ini?"
      : "Hello, I'm your AI assistant. How are you feeling today?";
    
    const utterance = new SpeechSynthesisUtterance(testText);
    
    if (tempSettings.voice) {
      utterance.voice = tempSettings.voice;
    }
    utterance.rate = tempSettings.rate;
    utterance.pitch = tempSettings.pitch;
    utterance.lang = tempSettings.language;
    
    utterance.onend = () => {
      setIsTestPlaying(false);
    };
    
    utterance.onerror = () => {
      setIsTestPlaying(false);
    };
    
    speechSynthesis.speak(utterance);
  };

  const saveSettings = () => {
    onSettingsChange(tempSettings);
    onClose();
  };

  const resetToDefaults = () => {
    const defaultSettings: VoiceSettings = {
      voice: voices.find(voice => voice.lang.startsWith('en')) || voices[0] || null,
      rate: 1,
      pitch: 1,
      language: "en-US"
    };
    setTempSettings(defaultSettings);
  };

  // Filter voices by language for better UX
  const getVoicesForLanguage = () => {
    const langCode = tempSettings.language.split('-')[0];
    return voices.filter(voice => voice.lang.startsWith(langCode));
  };

  const getVoiceQualityLabel = (voice: SpeechSynthesisVoice) => {
    if (voice.localService) return "High Quality";
    return "Standard";
  };

  return (
    <Dialog open onOpenChange={onClose}>
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle>Voice Settings</DialogTitle>
          <DialogDescription>
            Customize your AI assistant's voice to make conversations more comfortable
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-6">
          {/* Language Selection */}
          <div className="space-y-2">
            <Label>Language</Label>
            <Select value={tempSettings.language} onValueChange={handleLanguageChange}>
              <SelectTrigger>
                <SelectValue placeholder="Select language" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="en-US">English (US)</SelectItem>
                <SelectItem value="en-GB">English (UK)</SelectItem>
                <SelectItem value="id-ID">Indonesian</SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/* Voice Selection */}
          <div className="space-y-2">
            <Label>Voice</Label>
            <Select 
              value={tempSettings.voice?.name || ""} 
              onValueChange={handleVoiceChange}
            >
              <SelectTrigger>
                <SelectValue placeholder="Select voice" />
              </SelectTrigger>
              <SelectContent>
                {getVoicesForLanguage().map((voice) => (
                  <SelectItem key={voice.name} value={voice.name}>
                    <div className="flex items-center justify-between w-full">
                      <span>{voice.name}</span>
                      <span className="text-xs text-muted-foreground ml-2">
                        {getVoiceQualityLabel(voice)}
                      </span>
                    </div>
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          {/* Speech Rate */}
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <Label>Speech Rate</Label>
              <span className="text-sm text-muted-foreground">{tempSettings.rate.toFixed(1)}x</span>
            </div>
            <Slider
              value={[tempSettings.rate]}
              onValueChange={handleRateChange}
              min={0.5}
              max={2}
              step={0.1}
              className="w-full"
            />
            <div className="flex justify-between text-xs text-muted-foreground">
              <span>Slow</span>
              <span>Normal</span>
              <span>Fast</span>
            </div>
          </div>

          {/* Pitch */}
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <Label>Pitch</Label>
              <span className="text-sm text-muted-foreground">{tempSettings.pitch.toFixed(1)}</span>
            </div>
            <Slider
              value={[tempSettings.pitch]}
              onValueChange={handlePitchChange}
              min={0.5}
              max={2}
              step={0.1}
              className="w-full"
            />
            <div className="flex justify-between text-xs text-muted-foreground">
              <span>Low</span>
              <span>Normal</span>
              <span>High</span>
            </div>
          </div>

          {/* Test Voice */}
          <Card>
            <CardHeader>
              <CardTitle className="text-sm">Test Voice</CardTitle>
            </CardHeader>
            <CardContent>
              <Button 
                onClick={testVoice} 
                variant="outline" 
                className="w-full"
                disabled={!tempSettings.voice}
              >
                {isTestPlaying ? "Stop Test" : "Test Voice"}
              </Button>
            </CardContent>
          </Card>
        </div>

        {/* Action Buttons */}
        <div className="flex justify-between pt-4">
          <Button variant="outline" onClick={resetToDefaults}>
            Reset Defaults
          </Button>
          <div className="space-x-2">
            <Button variant="outline" onClick={onClose}>
              Cancel
            </Button>
            <Button onClick={saveSettings}>
              Save Settings
            </Button>
          </div>
        </div>

        <div className="text-xs text-muted-foreground text-center pt-2 border-t">
          <p>Voice availability depends on your device and browser support</p>
        </div>
      </DialogContent>
    </Dialog>
  );
}
